.header{
    margin:0px;
    font-family: 'MyFont';
    
}
.content{
  margin:0px;
  font-family: 'MyFont';
  
} 
@font-face {
    font-family: 'MyFont';
    src: local('MyFont'), url(../font/Asem.woff) format('woff');
  }
.App{
    display:flex;
    flex-direction: row;
    text-align: unset;
}
.header{
    width:50vw;
    height: 100vh;
    display:flex;
    align-items: center;
    justify-content: center;
    color:white;
    font-weight: bold;
    background-color: #BDBDBD;
}
.content{
    width:50vw;
    height: 100vh;
    display:flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    
    
  
    
}
.header h1{
    font-size: 5rem;
}
.content div:nth-child(odd){
  margin-right: 100px;
  margin-left: 100px;
}
.content div:nth-child(even){
  margin-right: 100px;
  margin-left: 100px;
}
.content div{
    margin-top: 100px;
    display:flex;
    border-radius: 50px;
    
    transition:1s;
    align-items: center;
    justify-content: center;
    height: 150px;
    width:150px;
    cursor: pointer;
    font-weight: bold;
    font-size:30px;
    
}
.content div:nth-child(2){
    
    
    background-color: #A6CE39 ;
    color:#A6CE39;
    
    
}
.content div:nth-child(1):hover{
  background-color: white ;
}
.content div:nth-child(2):hover{
  background-color: white ;
}
.content div:nth-child(3):hover{
  background-color: white ;
}

.content div:nth-child(1){
  
  
  background-color: #A9F5A9;
  color:#A9F5A9;

  
}

.content div:nth-child(3){
  
  
  background-color: #EAE2D7;
  color:#EAE2D7;
  
}
.spanText{
  margin-top: 30px;
  display: none;
  flex-direction: column;

}
.image_android{
    width:0vw;
    height: 0vh;
    position: absolute;
    transition: 1s;
    background-size: contain;
    background-color: white;
    display:flex;
    flex-direction: row;
}
.image_android .image-div{
  align-items: baseline;
}
.image_hobby{
  width:0vw;
  height: 0vh;
  position: absolute;
  transition: 1s;
  background-size: contain;
  background-color: white;

}
.image-content{
  transition: 1s;
  width: 0vw;
  height: 0vh;
  background-repeat: no-repeat;
  background-size: contain;
}

.image-div{
  display:flex;
  align-items: center;
  justify-content: center;
    width: 0vw;
    height: 0vh;
    margin-left: 50px;
    
    

    
}

/*-------------------------------------------------cancel button css ---------------------------------------------------------*/



.image-button{
  display:none;
  width: 70px;
  height: 70px;
  float: right;
}
.button-block {

  border-radius: 100%; /* TODO: prefix */
  margin: 0 auto;
  position: relative;
  display: inline-block;
  
  border: 35px solid;
  
  border-color: rgba( 255, 255, 255, 0 );
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  box-sizing: border-box;
  -webkit-transition: border 600ms ease-in-out;
  -moz-transition: border 600ms ease-in-out;
  -ms-transition: border 600ms ease-in-out;
  -o-transition: border 600ms ease-in-out;
  transition: border 600ms ease-in-out;
}
.button-block button {
  position: absolute;

  width: 30px;
  height: 30px;
  background: transparent;
  border: 0;
  margin: -15px 0 0 -15px;
  cursor: pointer;
}
.mark {
  background-color: black;
  position: absolute;
  height: 30px;
  width: 4px;
  
  top: 0;
  -webkit-transition: all 600ms ease-in-out;
  -moz-transition: all 600ms ease-in-out;
  -ms-transition: all 600ms ease-in-out;
  -o-transition: all 600ms ease-in-out;
  transition: all 600ms ease-in-out;
}
.mark.x  { 
  opacity:0.7;
  right: 14px; 
  transform:rotate(45deg); /* TODO: prefix */
}
.mark.xx { 
  opacity:0.7;
  left: 12px; 
  transform:rotate(-45deg); /* TODO: prefix */
}






